import React from 'react';
import  {BrowserRouter as Router , Routes, Route, Link } from "react-router-dom"
import { useState,useEffect,useContext } from 'react';
import './App.css'
import Nav from './components/nav';
import Footer from './components/footer';
import Main from './components/main';
import { ExploreData, UserDetails } from './components/useContext';
import ForwardPost from './components/search/share/share-Overlay';
import ErrorPage from './components/ErrorPage';
import Explore from './explore/Explore';

const url1 = 'https://randomuser.me/api/?results=3'
const url2 = 'https://randomuser.me/api/?results=10'


     /* FETCH DATA FROM API */
function App() {
  const [count, setCount] = useState(10)
  const url3 = `https://randomuser.me/api/?results=10`
  const [users, setUsers] = useState([])
  const [story, setStory] = useState([])
  const [explore, setExplore] = useState([])
  

  useEffect(()=> {
    fetch(url1)
    .then(res => res.json())
    .then(data => setUsers(data.results))
  },[])

  useEffect(()=> {
    fetch(url2)
    .then(res => res.json())
    .then(data => setStory(data.results))
  },[])
  useEffect(()=> {
    fetch(url3)
    .then(res => res.json())
    .then(data => setExplore(data.results))
  },[])

  function empty() {
 return ''
}
  return (
    <div className="App">
       {/* USE useContext TO PUSH THE DATA TO THE CHILDREN */}





<Router>
  <Routes>
    <Route path='/' element={
<UserDetails.Provider value={{users,empty, story}}>
<Nav/>
<Main />
</UserDetails.Provider>} />
<Route path='/explore' element={
  <ExploreData.Provider value={{explore}}>
<Explore />
</ExploreData.Provider>} />
<Route path='*' element={<ErrorPage   Link={Link} />}/>
  </Routes>
  <Footer Link={Link}/>  
</Router>

    </div>
  )
  }

export default App;
